import React, {useState} from 'react';
import {connect} from 'react-redux';

import {closePopout, goBack, openModal, openPopout, setPage} from '../../store/router/actions';

import {
    Panel,
    Group,
    PanelHeader,
    FixedLayout,
    Title,
    Placeholder,
    Button,
    PanelSpinner,
    Tabs,
    Snackbar, Avatar, FormLayout, FormStatus, Header, Banner, SimpleCell, Cell, PanelHeaderButton
} from "@vkontakte/vkui"
import bridge from "@vkontakte/vk-bridge";
import {
    Icon16Done,
    Icon16ErrorCircleFill,
    Icon20CheckCircleFillGreen, Icon20StatisticsOutline,
    Icon28ViewOutline,
    Icon32ErrorCircleOutline
} from "@vkontakte/icons";
import axios from "axios";

class HomePanelBase extends React.Component {

    state = {
        course: 100,
        logo: null,
        disableButton: true,
        snackbar: null,
        isLoading: true,
        profileData: {
            status: false,
            shilings: 0.0,
            shilings_bonus: 0.0,
        }
    }

    componentDidMount() {
        this.getFetch();
        setTimeout(() => this.setState({disableButton: false}), 1000);
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props !== prevProps) {
            this.getFetch();
        }
    }

    async getFetch() {
        const data = new FormData();
        data.append('action', 'getData');

        const request = await axios.post(`${this.props.server}`, data, this.props.server_confg);

        if(request.data.status === 'success') {
            this.setState({
                course: request.data.course,
                logo: request.data.logo,
                profileData: {
                    status: 1,
                    shilings_bonus: request.data.shilings_bonus
                },
            });
        }

        this.setState({
            isLoading: false
        });
    }

    reload = async () => {
        this.setState({
            isLoading: true
        });
        await this.getFetch();
    }

    viewAd = async () => {
        if (!bridge.supports('VKWebAppShowNativeAds')) return;

        this.setState({disableButton: true});
        setTimeout(() => this.setState({disableButton: false}), 2000);

        return bridge.send('VKWebAppShowNativeAds', { ad_format: 'preloader' })
            .then(async ad => {
                if (!ad.result) {
                    this.setState({
                        snackbar:
                            <Snackbar
                                onClose={() => this.setState({snackbar: null})}
                                before={<Avatar size={28}><Icon16ErrorCircleFill width={28} height={28}/></Avatar>}
                            >
                                Реклама не просмотрена, бонус не выдан.
                            </Snackbar>
                    });
                    return false;
                }

                const data = new FormData();
                data.append('action', 'viewAd');

                await axios.post(`${this.props.server}`, data, this.props.server_confg);

                this.setState({
                    profileData: {
                        status: 1,
                        shilings_bonus: this.state.profileData.shilings_bonus + 1
                    },
                });

                return true;
            })
            .catch(() => {
                this.setState({
                    snackbar:
                        <Snackbar
                            onClose={() => this.setState({snackbar: null})}
                            before={<Avatar size={28}><Icon16ErrorCircleFill width={28} height={28}/></Avatar>}
                        >
                            Реклама не просмотрена, бонус не выдан.
                        </Snackbar>
                });

                return false;
            });
        return true;
    }

    transfer = async () => {
        const data = new FormData();
        data.append('action', 'withdraw');

        const request = await axios.post(`${this.props.server}`, data, this.props.server_confg);

        if(request.data.status === 'success') {
            this.setState({
                snackbar:
                    <Snackbar
                        onClose={() => this.setState({snackbar: null})}
                        before={<Avatar size={28}><Icon20CheckCircleFillGreen width={28} height={28}/></Avatar>}
                    >
                        Баллы были отправлены на ваш аккаунт
                    </Snackbar>
            });

            this.setState({
                profileData: {
                    status: 1,
                    shilings_bonus: 0
                },
            });
        } else {
            this.setState({
                snackbar:
                    <Snackbar
                        onClose={() => this.setState({snackbar: null})}
                        before={<Avatar size={28}><Icon16ErrorCircleFill width={28} height={28}/></Avatar>}
                    >
                        {request.data.message}
                    </Snackbar>
            });
        }
    }

    render() {
        const {id, setPage, withoutEpic} = this.props;

        return (
            <Panel id={id}>
                <PanelHeader
                    left={<PanelHeaderButton><Icon20StatisticsOutline height={24} width={24} /></PanelHeaderButton>} onClick={() => this.props.openModal("MODAL_PAGE_BOTS_LIST")}
                >
                    Главная страница
                </PanelHeader>
                {this.state.isLoading && <PanelSpinner />}
                {!this.state.isLoading && this.state.profileData.status &&
                <div>
                    <Group>
                        <Placeholder
                            stretched
                            icon={<img src={this.state.logo}></img>}
                            action= {
                                bridge.supports('VKWebAppShowNativeAds')
                                    ?
                                    (
                                        <div>
                                            <Cell>
                                                <Button stretched before={<Icon28ViewOutline />} size='l' onClick={() => this.viewAd()} disabled={this.state.disableButton}>Заработать очки</Button>
                                            </Cell>
                                            <Cell>
                                                <Button stretched size="l" mode='secondary' size='l' onClick={() => this.transfer()}>Перевести средства на аккаунт</Button>
                                            </Cell>
                                        </div>
                                    )
                                    :
                                    (<Button stretched mode='outline' size='l' disabled>Данная функция доступна только с мобильного телефона</Button>)
                            }
                            header= {'Получи бесплатную привилегию!'}
                        >
                            За каждый просмотр рекламы вы получаете 1 очко<br />
                            {this.state.course} очков = 1 рубль
                        </Placeholder>

                        <Group>
                            <center>
                                <Title level='2' weight='regular' style={{ marginTop: 16, marginBottom: 4, color: 'var(--dynamic_gray)' }}>
                                    Очки
                                </Title>
                                <Title level='1' weight='bold' style={{ marginBottom: 16, color: 'var(--text_primary)' }}>
                                    {this.state.profileData.shilings_bonus}
                                </Title>
                            </center>
                        </Group>
                    </Group>
                </div>
                }
                {!this.state.isLoading && !this.state.profileData.status &&
                <div>
                    <Group style={{ height: '1000px' }}>
                        <Placeholder
                            icon={<Icon32ErrorCircleOutline width={56} height={56} fill="rgb(255, 0, 4)"/>}
                            header="Вы не привязали эту страницу ВК к профилю форума"
                            action={<Button size="m" onClick={this.reload} mode="outline">Попробовать еще раз</Button>}>
                        </Placeholder>
                    </Group>
                </div>
                }
                {this.state.snackbar}
            </Panel>
        );
    }

}

const mapDispatchToProps = {
    setPage,
    goBack,
    openPopout,
    closePopout,
    openModal
};

export default connect(null, mapDispatchToProps)(HomePanelBase);
