import React, {useState} from 'react';
import {connect} from 'react-redux';

import {closePopout, goBack, openModal, openPopout, setPage} from '../../store/router/actions';

import {
    Panel,
    Group,
    PanelHeader,
    FixedLayout,
    Title,
    Placeholder,
    Button,
    PanelSpinner,
    Tabs,
    Snackbar, Avatar, FormLayout, FormStatus, Header, Banner, SimpleCell, Cell
} from "@vkontakte/vkui"
import bridge from "@vkontakte/vk-bridge";
import {
    Icon16Done,
    Icon16ErrorCircleFill,
    Icon20CheckCircleFillGreen,
    Icon28ViewOutline,
    Icon32ErrorCircleOutline
} from "@vkontakte/icons";
import axios from "axios";

class NewsPanelBase extends React.Component {

    render() {
        const {id, setPage, withoutEpic} = this.props;

        return (
            <Panel id={id}>
                <PanelHeader>
                    Последние новости
                </PanelHeader>
                <div>
                    <img className="g_sticker" src="https://cs-legenda.ru/files/stickers/stich/8752.png"/>
                </div>
            </Panel>
        );
    }

}

const mapDispatchToProps = {
    setPage,
    goBack,
    openPopout,
    closePopout,
    openModal
};

export default connect(null, mapDispatchToProps)(NewsPanelBase);
