import React, {useState} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux'
import {goBack, closeModal, setStory} from "./js/store/router/actions";
import {getActivePanel} from "./js/services/_functions";
import * as VK from './js/services/VK';

import {Epic, View, Root, Tabbar, ModalRoot, TabbarItem, ConfigProvider} from "@vkontakte/vkui";

import HomePanelBase from './js/panels/home/base';
import ProfilePanelBase from "./js/panels/profile/base";
import NewsPanelBase from "./js/panels/news/base";

import {Icon28HomeOutline, Icon28MoneyTransferOutline, Icon28UserCircleOutline} from "@vkontakte/icons";
import HomeBotsListModal from "./js/components/modals/HomeBotsListModal";

const server = 'http://a0513794.xsph.ru/api.php'+window.location.search;
const server_confg = {
    headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
    }
}

class App extends React.Component {
    state = {
        balance: 100
    };

    constructor(props) {
        super(props);
        this.lastAndroidBackAction = 0;
    }z

    componentDidMount() {
        const {goBack, dispatch} = this.props;

        dispatch(VK.initApp());

        window.onpopstate = () => {
            let timeNow = +new Date();

            if (timeNow - this.lastAndroidBackAction > 500) {
                this.lastAndroidBackAction = timeNow;

                goBack();
            } else {
                window.history.pushState(null, null);
            }
        };
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        const {activeView, activeStory, activePanel, scrollPosition} = this.props;

        if (
            prevProps.activeView !== activeView ||
            prevProps.activePanel !== activePanel ||
            prevProps.activeStory !== activeStory
        ) {
            let pageScrollPosition = scrollPosition[activeStory + "_" + activeView + "_" + activePanel] || 0;

            window.scroll(0, pageScrollPosition);
        }
    }

    render() {
        const {goBack, setStory, closeModal, popouts, activeView, activeStory, activeModals, panelsHistory, colorScheme} = this.props;

        let history = (panelsHistory[activeView] === undefined) ? [activeView] : panelsHistory[activeView];
        let popout = (popouts[activeView] === undefined) ? null : popouts[activeView];
        let activeModal = (activeModals[activeView] === undefined) ? null : activeModals[activeView];

        const homeModals = (
            <ModalRoot activeModal={activeModal}>
                <HomeBotsListModal
                    id="MODAL_PAGE_BOTS_LIST"
                    onClose={() => closeModal()}
                    server={server}
                    server_confg={server_confg}
                />
            </ModalRoot>
        );

        return (
            <ConfigProvider isWebView={true} scheme={colorScheme}>
                <Epic activeStory={activeStory} tabbar={
                    <Tabbar>
                        <TabbarItem
                            onClick={() => setStory('home', 'base')}
                            selected={activeStory === 'home'}
                            text="Главная"
                        ><Icon28HomeOutline/></TabbarItem>
                        <TabbarItem
                            onClick={() => setStory('profile', 'base')}
                            selected={activeStory === 'profile'}
                            text="Профиль"
                        ><Icon28UserCircleOutline /></TabbarItem>
                    </Tabbar>
                }>
                    <Root id="home" activeView={activeView} popout={popout}>
                        <View
                            id="home"
                            modal={homeModals}
                            activePanel={getActivePanel("home")}
                            history={history}
                            onSwipeBack={() => goBack()}
                        >
                            <HomePanelBase
                                id="base"
                                withoutEpic={false}
                                server={server}
                                server_confg={server_confg}
                            />
                        </View>
                    </Root>
                    <Root id="profile" activeView={activeView} popout={popout}>
                        <View
                            id="profile"
                            modal={homeModals}
                            activePanel={getActivePanel("profile")}
                            history={history}
                            onSwipeBack={() => goBack()}
                        >
                            <ProfilePanelBase
                                id="base"
                                withoutEpic={false}
                                server={server}
                                server_confg={server_confg}
                            />
                        </View>
                    </Root>
                    <Root id="news" activeView={activeView} popout={popout}>
                        <View
                            id="news"
                            modal={homeModals}
                            activePanel={getActivePanel("news")}
                            history={history}
                            onSwipeBack={() => goBack()}
                        >
                            <NewsPanelBase
                                id="base"
                                withoutEpic={false}
                                server={server}
                                server_confg={server_confg}
                            />
                        </View>
                    </Root>
                </Epic>
            </ConfigProvider>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        activeView: state.router.activeView,
        activeStory: state.router.activeStory,
        panelsHistory: state.router.panelsHistory,
        activeModals: state.router.activeModals,
        popouts: state.router.popouts,
        scrollPosition: state.router.scrollPosition,

        colorScheme: state.vkui.colorScheme
    };
};


function mapDispatchToProps(dispatch) {
    return {
        dispatch,
        ...bindActionCreators({setStory, goBack, closeModal}, dispatch)
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
