import React from 'react';
import {connect} from 'react-redux';

import {openModal} from "../../store/router/actions";

import {
    List,
    Cell,
    Avatar,
    ModalPage,
    ModalPageHeader,
    PanelHeaderButton,
    withPlatform,
    IOS,
    PanelHeader, PanelSpinner, Panel
} from "@vkontakte/vkui";

import Icon24Dismiss from '@vkontakte/icons/dist/24/dismiss';
import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';
import Icon24Chevron from '@vkontakte/icons/dist/24/chevron';
import axios from "axios";

class HomeBotsListModal extends React.Component {


    state = {
        users: null,
        isLoading: true,
    }

    componentDidMount() {
        this.getUsers();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props !== prevProps) {
            this.getUsers();
        }
    }

    async getUsers() {
        const data = new FormData();
        data.append('app_id', this.props.app_id);
        data.append('action', 'getTop');

        const request = await axios.post(`${this.props.server}`, data, this.props.server_confg);

        if(request.data.status == 'success') {
            this.setState({
                isLoading: false,
                users: request.data.users
            })
        }
    }

    render() {
        const {id, onClose, openModal, platform} = this.props;

        return (
            <ModalPage
                id={id}
                header={
                    <ModalPageHeader
                        left={platform !== IOS &&
                        <PanelHeaderButton onClick={onClose}><Icon24Cancel/></PanelHeaderButton>}
                        right={platform === IOS &&
                        <PanelHeaderButton onClick={onClose}><Icon24Dismiss/></PanelHeaderButton>}
                    >
                        Топ 5 игроков по очкам
                    </ModalPageHeader>
                }
                onClose={onClose}
                settlingHeight={80}
            >
                {this.state.isLoading &&
                <div>
                    <List>
                        <Cell
                            description="Получаем данные.."
                            before={<Avatar size={40}/>}
                        >
                            Загрузка данных
                        </Cell>
                        <Cell
                            description="Получаем данные.."
                            before={<Avatar size={40}/>}
                        >
                            Загрузка данных
                        </Cell>
                        <Cell
                            description="Получаем данные.."
                            before={<Avatar size={40}/>}
                        >
                            Загрузка данных
                        </Cell>
                        <Cell
                            description="Получаем данные.."
                            before={<Avatar size={40}/>}
                        >
                            Загрузка данных
                        </Cell>
                        <Cell
                            description="Получаем данные.."
                            before={<Avatar size={40}/>}
                        >
                            Загрузка данных
                        </Cell>
                    </List>
                </div>}
                {!this.state.isLoading && <div>
                    <List>
                        {this.state.users.map((user, index) => (
                            <Cell
                                key={index}
                                description={user.score}
                                before={<Avatar size={40} src={user.avatar}/>}
                            >
                                {user.name}
                            </Cell>
                        ))}
                    </List>
                </div>}
            </ModalPage>
        );
    }

}

const mapDispatchToProps = {
    openModal
};

export default withPlatform(connect(null, mapDispatchToProps)(HomeBotsListModal));
