import React, {useEffect, useState} from 'react';
import {connect} from 'react-redux';

import {closePopout, goBack, openModal, openPopout, setPage} from '../../store/router/actions';

import {
    Panel,
    Group,
    PanelHeader,
    FixedLayout,
    Title,
    Placeholder,
    Button,
    SimpleCell,
    Avatar,
    MiniInfoCell, Separator, RichCell, PanelSpinner, Gradient, Text
} from "@vkontakte/vkui"
import bridge from "@vkontakte/vk-bridge";
import {
    Icon20ArticleOutline, Icon20CommunityName,
    Icon20MentionOutline,
    Icon20MessageOutline,
    Icon20PlaceOutline, Icon28MessageOutline,
    Icon28ViewOutline, Icon32ErrorCircleOutline
} from "@vkontakte/icons";
import axios from "axios";

class ProfilePanelBase extends React.Component {

    state = {
        isLoading: true,
        profileData: {
            status: false,
            login: null,
            avatar: null,
            shilings: 0.0
        }
    }

    componentDidMount() {
        this.getFetch();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props !== prevProps) {
            this.getFetch();
        }
    }

    async getFetch() {
        const data = new FormData();
        data.append('action', 'getData');

        const request = await axios.post(`${this.props.server}`, data, this.props.server_confg);

        if(request.data.status == 'success') {
            this.setState({
                profileData: {
                    status: 1,
                    login: request.data.login,
                    avatar: request.data.avatar,
                    shilings: request.data.shilings
                },
            });
        }

        this.setState({
            isLoading: false
        });
    }

    reload = async () => {
        this.setState({
            isLoading: true
        });
        await this.getFetch();
    }

    render() {
        const {id, setPage, withoutEpic} = this.props;

        return (
            <Panel id={id}>
                <PanelHeader>Мой профиль</PanelHeader>
                {this.state.isLoading && <PanelSpinner />}
                {!this.state.isLoading && !this.state.profileData.status &&
                <div>
                    <Group style={{ height: '1000px' }}>
                        <Placeholder
                            icon={<Icon32ErrorCircleOutline width={56} height={56} fill="rgb(255, 0, 4)"/>}
                            header="Вы не привязали эту страницу ВК к профилю форума"
                            action={<Button size="m" onClick={this.reload} mode="outline">Попробовать еще раз</Button>}>
                        </Placeholder>
                    </Group>
                </div>
                }
                {!this.state.isLoading && this.state.profileData.status &&
                    <div>
                        <Group>
                            <Gradient style={{
                                margin: 0,
                                display: 'flex',
                                flexDirection: 'column',
                                alignItems: 'center',
                                justifyContent: 'center',
                                textAlign: 'center',
                                padding: 32,
                            }}>
                                <Avatar size={96} src={this.state.profileData.avatar}/>
                                <Title style={{ marginBottom: 8, marginTop: 20 }} level="2" weight="medium">{this.state.profileData.login}</Title>
                                <Text style={{ marginBottom: 24, color: 'var(--text_secondary)' }}>Мой профиль</Text>
                                <Button size="m" mode="secondary" disabled="true">Скоро тут будет что-то еще..</Button>
                            </Gradient>
                        </Group>
                    </div>
                }
            </Panel>
        );
    }

}

const mapDispatchToProps = {
    setPage,
    goBack,
    openPopout,
    closePopout,
    openModal
};

export default connect(null, mapDispatchToProps)(ProfilePanelBase);